.head {
    padding: 10px;
    width: 90vw;
    background-color: rgb(68, 68, 255);
    color: white;
}

.content .icon{
    height: 20px;
    width: 20px;
}

h3,p{
    margin: 5px;
}

.menu{
    text-align: center;
    border-bottom: 1px solid black;
    padding-bottom: 10px;
}

.grid-items {
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 20px;
    text-align: center;
    margin: 20px auto;
    width: 90%;
    color: white;
}

.grid-items div {
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}


.grid-items div:hover{
    transition: transform 0.3s ease;
    transform: scale(1.1); 
    color:black;
}

.regular {
    background-color: rgb(210, 0, 0);
}

.Remedial {
    background-color: purple;
}

.in-paid {
    background-color: green;
}

.enrollment {
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 20px;
    margin: 40px auto;
    width: 90%;
    text-align: center;
}

.enrollment div {
    background-color: white;
    border-radius: 8px;
    margin-bottom: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    padding-bottom: 5px;
}

.enrollment div:hover {
    transition: transform 0.3s ease;
    transform: scale(1.1);
    color: red;
}

.enrollment .icon{
    height: 25px;
    width: 25px;
}

.enrollment div p {
    margin-top: 10px;
}
