.navbar{
    background-color: rgb(0, 0, 184);
    width: max-content;
    padding: 10px;
    text-align: center;
    height: 100%;
}

ul{
    padding: 0;
    list-style-type: none;
}

li{
    color: white;
}

.icon{
    margin-top: 25px;
    width: 50px;
    height: 50px;
}

.logo img{
    margin-top: 10px;
    border-radius: 100px;
    width: 100px;
}

.bottom{
    color: white;
    display: grid;
    grid-template-columns: auto auto auto;
}

.bottom .icon{
    height: 25px;
    width: 25px;
}

li:hover,.icon:hover{
    color: red;
}